import { Component, OnInit } from '@angular/core';
import { Validations } from '../../../validations';
import { UserService } from '../../services/userService';
import swal from 'sweetalert2';

@Component({
  selector: 'app-lock-screen',
  providers:[UserService],
  templateUrl: './lock-screen.component.html',
  styleUrls: ['./lock-screen.component.scss']
})
export class LockScreenComponent implements OnInit {

  public Email:any;
  public validator:Validations;
  public emptyEmail:boolean  = false;
  public loader:boolean = false;
  public actionBtn:boolean  = true;
  public validEmail:boolean = false;

  
  constructor(public _userService:UserService) { 
    this.validator = new Validations();
  }

  ngOnInit() {
  }

  resetErrors(){
    this.emptyEmail = false;
    this.validEmail = false;
  }

  forgotPassword(){

    var error = false;
    this.resetErrors();

    if(!this.validator.verifyNameInputs(this.Email)){
      this.emptyEmail = true;
      error = true;
    }else  if(!this.validator.validateEmail(this.Email)){
      this.validEmail = true;
      error = true;
    }


    if(error){

    }else{

      this.actionBtn = false;
      this.loader = true;

      this._userService.sendForgotPasswordToUser(this.Email).subscribe(a => {

        console.log(a);

        if(a.code == 200 ){
          this.actionBtn = true;
          this.loader = false;
          swal(a.message, "New password has been sent to your email", "success");
          this.Email = undefined;
        }else{
          this.actionBtn = true;
          this.loader = false;
          swal(a.message, "Error Occured", "error");
        }
        



      },error=>{
        this.actionBtn = true;
        this.loader = false;
        swal("Error", "", "error");
      })

    }
   
  }

}



